import React from 'react'
import { Radio } from 'antd'
import { inject, observer } from 'mobx-react';
import ServerMenu from './ServerMenu'
import SchemaMenu from './SchemaMenu'
import EditPage from '../cell/EditPage'

@inject('DeviceMaintainStore')
@observer
class CheckPage extends React.Component {
  constructor(props) {
    super(props)
    this.store = this.props.DeviceMaintainStore;
    this.state = {
      radio: 'server'
    }
  }
  render() {
    return <div>
      <Radio.Group onChange={this.handleChange} defaultValue={'server'}>
        <Radio.Button value='server'>故障类型库</Radio.Button>
        <Radio.Button value='schema'>故障类型方案</Radio.Button>
      </Radio.Group>
      {
        this.state.radio === 'server' ? <ServerMenu /> : <SchemaMenu />
      }
      {
        this.store.editVisible && <EditPage title={'故障类型'} />
      }
    </div>
  }
  handleChange = e => {
    this.setState({
      radio: e.target.value
    })
    this.store.editVisible = false;
  }
  componentDidMount() {
    this.store.getDevice();
  }
  componentWillUnmount() {
    this.store.editVisible = false;
  }
}

export default CheckPage;