import React, { Component } from 'react';
import { inject, observer } from 'mobx-react';
import GlobalList from 'components/GlobalList';
import { Modal, } from 'antd';
import { setServerColumns, } from '../cell/methods';


@inject('DeviceMaintainStore')
@observer
class ServerMenu extends Component {
	constructor(props) {
		super(props);
		this.store = this.props.DeviceMaintainStore;
		this.state = {
			selectedRowKeys: [],
		};
	}
	render() {
		const {
			serverList,
			isLoading,
		} = this.store;
		return (
			<div style={{ marginTop: '10px'}}>
				<GlobalList
					table={{
						rowKey: 'id',
						columns: setServerColumns({
							handleEdit:this.handleEdit,handleDelete:this.handleDelete,contentLabel:'保养'
						}),
						dataSource: serverList,
						pagination: false,
						loading: isLoading,
						btns: [
							{
								name: <div><i className="iconfont icon-add icon-spacing" style={{fontSize:'12px'}}></i>新增</div>,
								onClick: () => {
									this.store.status = 'add';
									this.handleVisible('add');
								},
								type: 'primary',
								className:'add-btn'
							}
						]
					}}
					
				/>
			</div>
		);
	}
	/* ------------弹窗的显隐 */
	handleVisible = () => {
		this.store.editVisible = !this.store.editVisible;
	};
	/* table - operate */
	handleEdit = value => {
		this.store.status = 'edit';
		this.store.getInfo({ id: value },'server').then(()=>{
			this.handleVisible();
		});
	};
	handleDelete = (value) => {
		Modal.confirm({
			title: '提示',
			content: '确定删除当前点检内容？',
			okText: '确定',
			cancelText: '取消',
			onOk: () => {
				this.store.delete(
					{
						id: value
					},
					'server'
				);
			}
		});
	};
	handleSelect = selectedRowKeys => {
		this.setState({ selectedRowKeys });
	};
	componentDidMount() {
		this.store.getMaintainServer({maintainType:3});
	}
}
export default ServerMenu;
